@contact-me: contact-me;
.@{contact-me}-wrapper {
  height: 480px;
  background: url("../../../../public/assets/contact-me-background.png") no-repeat bottom;
  background-size: cover;
  margin: 0 auto;
  overflow: hidden;
  padding-top: 96px;
  &.home-page-wrapper {
    .title-wrapper {
      margin-bottom: 32px;
    }
  }
  .button {
    box-shadow: 0 8px 16px #7aafd1;
    background: linear-gradient(to right, #7aafd1, #5899db) !important;
    height: 42px;
    line-height: 42px;
    font-size: 14px;
    border: 0;
    border-radius: 21px;
    color: #fff;
    width: fit-content !important; 
    padding: 0 15px;
    display: inline-block;
    transition: transform .3s, box-shadow .3s;
    &:hover {
      transform: translateY(-4px);
      box-shadow: 0 12px 20px #7aafd1;
      color: white;
    }
    &:active {
      transform: translateY(4px);
      box-shadow: 0 4px 8px #7aafd1;
    }
  }
  .title-content {
    line-height: 32px;
  }
}

@media screen and (max-width: 767px) {
  .@{contact-me}-wrapper {
    padding-bottom: 0;
  }
}
