.header {
  background: #fff;
  width: 100%;
  z-index: 1;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
  position: relative;
  top: 0;
}
.header .home-page {
  padding: 0 24px;
}
.header-logo {
  display: inline-block;
  position: relative;
  width: 150px;
  line-height: 64px;
}
.header-logo img {
  vertical-align: middle;
  display: inline-block;
}
.header-logo a {
  display: block;
}
.header-menu {
  float: right;
}
.header-menu .ant-menu-horizontal {
  border-bottom: none;
}
.header-menu .ant-menu {
  line-height: 62px;
  height: 64px;
  background: transparent;
}
.header-menu .ant-menu a {
  display: block;
}
.header-item-block {
  padding: 0 8px;
}
.header-item-block > * {
  display: inline-block;
}
.header-item.ant-menu-item:hover::after {
  border-bottom-color: #618cab !important;
}
.header-item.ant-menu-item-selected {
  color: #618cab !important;
}
.header-item.ant-menu-item-selected::after {
  border-bottom-color: #618cab !important;
}
.header-item .ant-menu-sub .ant-menu-item,
.header-item-child .ant-menu-sub .ant-menu-item,
.header-menu .ant-menu-sub .ant-menu-item,
.header-item .ant-menu-inline .ant-menu-item,
.header-item-child .ant-menu-inline .ant-menu-item,
.header-menu .ant-menu-inline .ant-menu-item {
  height: auto;
  line-height: 1.5;
}
.header-item .item-sub-item,
.header-item-child .item-sub-item,
.header-menu .item-sub-item {
  display: block;
  padding: 8px 24px;
}
.header-item .item-image,
.header-item-child .item-image,
.header-menu .item-image {
  float: left;
  margin-right: 16px;
  margin-top: 4px;
  position: relative;
  z-index: 1;
}
.header-item .item-title,
.header-item-child .item-title,
.header-menu .item-title {
  font-size: 14px;
  color: white;
  margin-left: 46px;
}
.header-item .item-content,
.header-item-child .item-content,
.header-menu .item-content {
  font-size: 12px;
  color: rgba(255, 255, 255, 0.75);
  margin-left: 46px;
}
@media screen and (max-width: 767px) {
  .header-logo {
    z-index: 101;
  }
  .header.home-page-wrapper .home-page {
    padding: 0 24px;
  }
  .header-menu {
    height: auto;
    float: inherit;
    position: relative;
    left: -24px;
    width: calc(100% + 48px);
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
    background: #fff;
  }
  .header-menu li {
    padding: 0 24px;
  }
  .header-menu li.ant-menu-submenu {
    padding: 0;
  }
  .header-menu .item-sub-item {
    padding: 8px 0;
  }
  .header-mobile-menu {
    width: 16px;
    height: 14px;
    cursor: pointer;
    position: absolute;
    top: 24px;
    right: 24px;
    z-index: 100;
  }
  .header-mobile-menu em {
    display: block;
    width: 100%;
    height: 2px;
    background: #001529;
    margin-top: 4px;
    transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;
  }
  .header-mobile-menu :first-child {
    margin-top: 0;
  }
  .header .ant-menu {
    height: auto;
    overflow: hidden;
  }
  .header .ant-menu .ant-menu-item-selected {
    border: none;
  }
  .header .open {
    height: auto;
  }
  .header .open .header-mobile-menu em:nth-child(1) {
    transform: translateY(6px) rotate(45deg);
  }
  .header .open .header-mobile-menu em:nth-child(2) {
    opacity: 0;
  }
  .header .open .header-mobile-menu em:nth-child(3) {
    transform: translateY(-6px) rotate(-45deg);
  }
  .header .open > .header-menu {
    opacity: 1;
    pointer-events: auto;
  }
  .header-item-block {
    height: 40px;
    line-height: 40px;
  }
}
